@import url(https://fonts.googleapis.com/css?family=Cinzel:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cinzel:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cinzel:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cinzel:700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.WineForm {
  width: 50%;
}

.wine-input{
  margin: auto;
  text-align: center;
}

.add-wine {
  color: white;
  font-family: 'cinzel', serif;
  font-size: 45px;
  text-align: center;
}

input {
  border: none;
  border-radius: 15px;
  height: 25px;
  padding: 5px;
  margin: 10px;
  text-align: center;
  width: 250px;
}

input[type="text"], input[type='number'] {
    font-size: 18px;
}

.add {
  background-color: #D3E303;
  border-radius: 25px;
  font-size: 15px;
  font-weight: bold;
  height: 35px;
  padding: 8px;
  margin: 20px;
  text-align: center;
  width: 300px;
}

.add:hover {
  cursor: pointer;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: grey;
  font-weight: bold;
  font-size: 15px;
}
::-moz-placeholder { /* Firefox 19+ */
  color: grey;
  font-weight: bold;
  font-size: 15px;
}
:-ms-input-placeholder { /* IE 10+ */
  color: grey;
  font-weight: bold;
  font-size: 15px;
}
:-moz-placeholder { /* Firefox 18- */
  color: grey;
  font-weight: bold;
  font-size: 15px;
}
.vino {
  background-color:rgba(255,255,255, 0.3);
  margin: 10px auto;
  width: 260px;
}

.vineyard {
  color: white;
  font-family: 'cinzel', serif;
  font-size: 25px;
  text-align: center;
}
.wine {
  color: white;
  font-family: 'cinzel', serif;
  font-size: 45px;
  text-align: center;
}

.wines {
  text-align: center;
  width: 50%;
}

.wine-container {
  display: flex;
  flex-direction: column;
  overflow: scroll;
  height: 500px;
}
.App {
  background-color: #282c34;
  background: -webkit-gradient( linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0.3)) ), url(/my-cellar-fe/static/media/background.a151d445.jpeg);
  background: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.3) ), url(/my-cellar-fe/static/media/background.a151d445.jpeg);
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  z-index:3;
}

h1{
  color: white;
  font-family: 'cinzel', serif;
  font-size: 65px;
  margin: 0;
  padding: 20px;
  text-align: center;
}

section {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: 10px;
}

